* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

body {
  background: #ececec;
  font-family: 'OpenSans-Regular';
}

img {
  width: 100%;
}

.wrap {
  max-width: 1230px;
  margin: auto;
  padding: 0 30px;
}

.header #burger1,
.header #burger2 {
  display: none;
  margin-left: auto;
}

.header .nav_menu_top {
  padding: 27px 0;
  background: #fff;
}

.header .nav_menu_top .wrap {
  flex-direction: column;
  align-items: stretch;
}

.header .wrap,
.header .collapse,
.header .menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .collapse {
  flex-grow: 1;
}

.header .btn-red {
  background: #d51e08;
  color: #fff;
  padding: 6px 20px;
  border-radius: 6px;
  display: inline-block;
}

.header .btn-red i {
  margin-right: 15px;
}

.header .menu {
  text-transform: uppercase;
  font-size: 14px;
}

.header .menu li {
  margin: 0 15px;
}

.header .menu li:hover {
  color: #d51e08;
  transition: .4s;
}

.header .nav_menu_carousel {
  position: relative;
}

.header .nav_menu_carousel .search-icon {
  display: none;
  cursor: pointer;
  font-size: 24px;
}

.header .nav_menu_carousel .wrap {
  position: absolute;
  z-index: 5;
  top: 30px;
  background: #000;
  color: #fff;
  padding: 20px 30px;
  border-radius: 3px;
  max-width: 1170px;
  left: 0;
  right: 0;
}

.header .nav_menu_carousel .wrap .logo {
  position: absolute;
  top: 0;
  left: 30px;
  display: flex;
}

.header .nav_menu_carousel .wrap .logo span {
  padding: 19px 0;
  text-transform: uppercase;
  font-size: 24px;
  font-family: 'OpenSans-Bold';
  font-weight: 100;
  display: inline-block;
  margin-left: 20px;
}

.header .nav_menu_carousel .wrap .logo img {
  margin-right: 15px;
}

.header .nav_menu_carousel .wrap .collapse {
  padding-left: 300px;
}

.header .nav_menu_carousel .wrap .collapse .menu {
  font-size: 16px;
  text-transform: capitalize;
}

.header .nav_menu_carousel .wrap .collapse .form {
  position: relative;
  max-width: 250px;
  flex-grow: 1;
}

.header .nav_menu_carousel .wrap .collapse .form .search {
  border: none;
  padding: 7px;
  border-radius: 3px;
  width: 100%;
  color: black;
  font-size: 14px;
}

.header .nav_menu_carousel .wrap .collapse .form button {
  position: absolute;
  border: none;
  background: none;
  padding: 7px;
  right: -1px;
  top: -1px;
  cursor: pointer;
  color: #666;
}

.section_slider .slider {
  position: relative;
}

.section_slider .slider .item .image {
  height: 716px;
}

.section_slider .slider .item .image img {
  object-fit: cover;
  object-position: center;
  height: 100%;
}

.section_slider .slider .item .caption {
  position: absolute;
  top: 280px;
  left: 200px;
  text-align: center;
  text-transform: capitalize;
  color: #fff;
}

.section_slider .slider .item .caption .title {
  font-family: 'OpenSans-Bold';
  font-weight: 100;
  font-size: 48px;
  text-shadow: 1px 0px 5px #000000;
}

.section_slider .slider .item .caption p {
  font-family: 'OpenSans-Regular';
  font-size: 30px;
  margin-top: 20px;
  margin-bottom: 40px;
  text-shadow: 1px 0px 5px #000000;
}

.section_slider .slider .item .caption .btn-add {
  text-transform: capitalize;
  background: #d51e08;
  border: none;
  border-radius: 2px;
  font-family: 'OpenSans-Regular';
  font-size: 24px;
  padding: 18px 35px;
}

.section_slider .slider .owl-dots {
  background: #fff;
  display: flex;
  margin: auto;
  position: absolute;
  padding: 11px;
  border: 1px solid #ccc;
  border-radius: 4px;
  left: 0;
  right: 0;
  width: 150px;
  justify-content: space-around;
  bottom: -14px;
}

.section_slider .slider .owl-dots .owl-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #ccc;
}

.section_slider .slider .owl-dots .owl-dot.active {
  background: #333;
}

.section_slogan {
  background: #fff;
  padding: 45px 0;
  text-align: center;
  font-size: 30px;
}

.section_main {
  border-top: 3px solid #ccc;
}

.section_main .wrap {
  display: grid;
  grid-template-columns: 3fr 9fr;
  grid-gap: 46px;
}

.section_main .wrap .sidebar {
  padding-top: 36px;
  border-right: 3px solid #ccc;
  margin-right: -10px;
  width: 25%;
  float: left;
}

.section_main .wrap .sidebar .widget {
  border-bottom: 3px solid #ccc;
  line-height: 30px;
}

.section_main .wrap .sidebar .widget .title {
  font-family: 'OpenSans-Bold';
  font-size: 24px;
  font-weight: 100;
  margin-bottom: 20px;
  margin-top: 10px;
}

.section_main .wrap .sidebar .widget .list {
  font-family: 'OpenSans-Regular';
  font-size: 16px;
  margin-bottom: 20px;
}

.section_main .wrap .sidebar .widget .list span {
  color: #5a9ed3;
}

.section_main .wrap .sidebar .widget:nth-child(2) .title {
  margin-top: 25px;
}

.section_main .wrap .sidebar .widget:nth-child(3) {
  border-bottom: none;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider {
  max-width: 222px;
  background-color: #fff;
  margin-top: 31px;
  margin-bottom: 20px;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider .owl-dots {
  margin-top: -1px;
  padding: 2px 0;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider .owl-dot {
  margin: -5px;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider .caption {
  border-bottom: 1px solid #ccc;
  text-align: center;
  line-height: 18px;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider .caption h2 {
  font-family: 'OpenSans-Bold';
  font-size: 14px;
  font-weight: 100;
  margin-top: 25px;
}

.section_main .wrap .sidebar .widget:nth-child(3) .slider .caption p {
  font-family: 'OpenSans-Regular';
  font-size: 12px;
  margin: 21px 0;
}

.section_main .wrap .sidebar .widget:nth-child(4) {
  border-bottom: none;
  text-transform: uppercase;
  font-family: 'OpenSans-Regular';
  line-height: 25px;
  font-size: 18px;
  text-align: center;
  background: white;
  max-width: 222px;
  padding-bottom: 30px;
}

.section_main .wrap .sidebar .widget:nth-child(4) p {
  margin: 27px;
}

.section_main .wrap .sidebar .widget:nth-child(4) p span {
  font-family: 'OpenSans-Bold';
  font-size: 28px;
  font-weight: 100;
}

.section_main .wrap .sidebar .widget:nth-child(4) .red-btn {
  font-family: 'OpenSans-Bold';
  font-size: 18px;
  letter-spacing: 0.6px;
  border-radius: 2px;
  background: red;
  color: white;
  padding: 5px 30px;
}

.section_main .wrap .sidebar .description {
  font-family: 'OpenSans-Regular';
  font-size: 14px;
}

.section_main .wrap .sidebar .description h2 {
  font-family: 'OpenSans-Bold';
  font-size: 24px;
  font-weight: 100;
  margin-top: 45px;
}

.section_main .wrap .sidebar .description p {
  margin-top: 30px;
  margin-bottom: 80px;
}

.section_main .wrap .sidebar .description p span {
  color: #0a94b2;
}

.section_main .wrap .content {
  padding-top: 50px;
}

.section_main .wrap .content .title {
  font-family: 'OpenSans-Bold';
  font-size: 24px;
  font-weight: 100;
}

.section_main .wrap .content .cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 25px;
  text-align: center;
  margin: 45px 0;
}

.section_main .wrap .content .cards .card {
  border: 1px solid #ccc;
  background: #fff;
  padding: 30px 10px;
  min-width: 270px;
  min-height: 440px;
  display: flex;
  flex-direction: column;
}

.section_main .wrap .content .cards .card i {
  display: none;
}

.section_main .wrap .content .cards .card .sale {
  position: absolute;
  margin-top: -15px;
  margin-left: 6px;
  text-transform: uppercase;
  font-family: 'OpenSans-Regular';
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 50%;
  padding: 15px 10px;
}

.section_main .wrap .content .cards .card .image {
  margin: auto;
  min-height: 200px;
}

.section_main .wrap .content .cards .card .title {
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 5px;
  text-transform: uppercase;
}

.section_main .wrap .content .cards .card .desc {
  font-family: 'OpenSans-Regular';
  font-size: 14px;
  min-height: 42px;
}

.section_main .wrap .content .cards .card .price {
  font-family: 'OpenSans-Bold';
  font-size: 18px;
  font-weight: 100;
  color: red;
  margin-top: auto;
}

.section_main .wrap .content .cards .card:hover {
  box-shadow: 0px 0px 7px 4px #ccc;
  transition: .5s;
  cursor: pointer;
}

.section_main .wrap .content .cards .card:hover .image {
  opacity: .5;
  transition: .5s;
}

.section_main .wrap .content .cards .card:hover i {
  display: block;
  position: absolute;
  margin: -12px 210px;
}

.section_main .wrap .content .cards .card:hover .btn-add {
  background-color: #ececec;
  transition: .5s;
}

.section_main .wrap .content .reclame {
  display: flex;
  justify-content: space-between;
  margin-top: -15px;
  margin-bottom: 35px;
}

@supports (display: grid) {
  .section_main .wrap .sidebar {
    width: auto;
    float: none;
  }
}

.section_discount {
  background-color: #f5f5f5;
  padding: 45px 0;
}

.section_discount .wrap {
  display: flex;
  justify-content: space-between;
}

.section_discount .wrap p {
  font-family: 'OpenSans-Light';
  font-size: 18px;
}

.section_discount .wrap p span {
  font-size: 24px;
}

.section_discount .wrap .name {
  border: 1px solid #ccc;
  border-radius: 7px;
  min-width: 290px;
  min-height: 50px;
  padding: 0 20px;
  font-family: 'OpenSans-Light';
  font-size: 14px;
}

.section_discount .wrap .email {
  border: 1px solid #ccc;
  border-radius: 7px;
  min-width: 290px;
  min-height: 50px;
  padding: 0 20px;
  font-family: 'OpenSans-Light';
  font-size: 14px;
}

.section_discount .wrap a {
  min-width: 170px;
  min-height: 50px;
  border-radius: 5px;
  background-color: #e77553;
  text-align: center;
  padding-top: 15px;
  box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.2);
  color: white;
  font-family: 'OpenSans-Bold';
  font-size: 18px;
  font-weight: 100;
}

.section_brand {
  background: #fff;
  border-top: 1px solid #aaa;
  padding: 10px 0;
}

.section_brand .slider {
  max-width: 1100px;
  margin: auto;
}

.section_brand .slider .owl-stage {
  display: flex;
  justify-content: center;
  align-items: center;
}

.section_brand .slider .owl-stage .owl-item {
  margin: 0 20px;
}

.section_brand .owl-nav {
  margin: 0;
}

.section_brand .owl-nav .owl-prev {
  position: absolute;
  top: 30px;
  left: -40px;
}

.section_brand .owl-nav .owl-next {
  position: absolute;
  top: 30px;
  right: -40px;
}

.section_brand .owl-nav i {
  padding: 0 2px;
}

.section_brand .owl-dots {
  display: none;
}

.footer_top {
  background-color: #343434;
  color: #ccc;
}

.footer_top .items {
  padding: 36px 0;
  display: flex;
  justify-content: space-between;
}

.footer_top .items .title {
  font-family: 'OpenSans-Bold';
  font-size: 14px;
  font-weight: 100;
  text-transform: uppercase;
  margin-bottom: 25px;
}

.footer_top .items a {
  font-family: 'OpenSans-Regular';
  font-size: 14px;
}

.footer_top .items a img {
  width: 46px;
  margin: 0 2px;
}

.footer_bottom {
  background-color: #fff;
}

.footer_bottom .items {
  padding: 22px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer_bottom .items p {
  font-family: 'OpenSans-Regular';
  font-size: 14px;
}

.footer_bottom .items a {
  margin-right: 3px;
}

.footer_bottom .items a img {
  width: 55px;
}

.btn-add {
  font-family: 'OpenSans-Regular';
  font-size: 14px;
  text-transform: uppercase;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 10px;
  background-color: #fff;
  margin-top: 20px;
  margin-left: 55px;
  width: 140px;
}

@media (max-width: 1200px) {
  .section_main .wrap {
    grid-template-columns: 1fr;
  }

  .section_main .wrap .sidebar {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    border: none;
    padding-bottom: 30px;
  }

  .section_main .wrap .sidebar .widget:nth-child(1) {
    grid-column: 2;
    grid-row: 1;
    border: none;
    margin-left: 20px;
  }

  .section_main .wrap .sidebar .widget:nth-child(1) .title {
    margin-top: 0;
    margin-bottom: 5px;
  }

  .section_main .wrap .sidebar .widget:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
    border: none;
    margin-left: 40px;
  }

  .section_main .wrap .sidebar .widget:nth-child(2) .title {
    margin-top: 0;
    margin-bottom: 5px;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) {
    grid-column: 1;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) .slider {
    margin-top: 0;
    margin-bottom: 0;
  }

  .section_main .wrap .sidebar .widget:nth-child(4) {
    grid-column: 4;
    grid-row: 1;
    padding-top: 70px;
    margin-left: 50px;
  }

  .section_main .wrap .sidebar .description {
    display: none;
  }

  .header .nav_menu_carousel {
    margin: 0 30px;
  }

  .section_discount .wrap p {
    font-size: 14px;
  }

  .section_discount .wrap p span {
    font-size: 18px;
  }

  .section_discount .wrap .name,
  .section_discount .wrap .email {
    min-width: 240px;
    min-height: 40px;
    font-size: 13px;
  }

  .section_discount .wrap a {
    min-width: 150px;
    min-height: 40px;
    font-size: 16px;
    padding-top: 10px;
  }

  .section_brand .owl-nav {
    display: none;
  }
}

@media (max-width: 1100px) {
  .header .nav_menu_carousel .form {
    display: none;
  }

  .header .nav_menu_carousel .search-icon {
    display: block;
  }
}

@media (max-width: 992px) {
  .section_main .wrap .sidebar {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border: none;
    justify-self: center;
    grid-gap: 20px;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) {
    grid-column: 1;
    grid-row: 1;
  }

  .section_main .wrap .sidebar .widget:nth-child(4) {
    grid-column: 3;
    grid-row: 1;
  }

  .section_main .wrap .sidebar .widget:nth-child(1) {
    grid-column: 2;
    grid-row: 1;
  }

  .section_main .wrap .sidebar .widget:nth-child(2) {
    grid-column: 2;
    grid-row: 2;
    margin-top: -150px;
    margin-left: 20px;
  }

  .section_main .wrap .sidebar .description {
    display: none;
  }

  .section_main .wrap .content .title {
    text-align: center;
  }

  .section_main .wrap .content .cards {
    grid-gap: 10px;
  }

  .section_main .wrap .content .cards .card {
    max-width: auto;
    max-height: auto;
    border: 3px solid red;
  }

  .header .nav_menu_top .menu {
    flex-direction: column;
    align-items: start;
  }

  .header .nav_menu_top .menu li {
    margin: 5px 0;
  }

  .header .nav_menu_carousel .form {
    display: block;
  }

  .header .nav_menu_carousel .search-icon {
    display: none;
  }

  .header .nav_menu_carousel .collapse,
  .header .nav_menu_carousel .menu {
    flex-direction: column;
    padding-left: 0 !important;
    align-items: stretch !important;
  }

  .header .nav_menu_carousel .wrap {
    height: 65px;
  }

  .header .nav_menu_carousel .wrap.show {
    height: auto;
  }

  .header .nav_menu_carousel .wrap.show .logo img {
    width: 50px;
  }

  .header .nav_menu_carousel .wrap .logo img {
    width: 100px;
    transition: .4s;
  }

  .header .nav_menu_carousel .collapse {
    display: none;
    margin-top: 49px;
  }

  .header .nav_menu_carousel .collapse.show {
    display: block;
  }

  .header .nav_menu_carousel .menu {
    line-height: 35px;
  }

  .header .nav_menu_carousel .menu li {
    margin: 0px;
  }

  .header .nav_menu_carousel .form {
    max-width: none !important;
  }

  .header .nav_menu_carousel #burger2 {
    display: block;
    position: absolute;
    right: 30px;
    top: 15px;
  }

  .section_slogan {
    font-size: 4vmax;
  }

  .section_discount .wrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .section_discount .wrap p {
    grid-column: 1 / 3;
    text-align: center;
    margin-top: -30px;
    font-size: 16px;
  }

  .section_discount .wrap p span {
    font-size: 20px;
  }

  .section_discount .wrap .name,
  .section_discount .wrap .email {
    max-width: 480px;
    margin: 10px;
  }

  .section_discount .wrap a {
    grid-column: 1 / 3;
    max-width: 960px;
    margin: 0 10px -30px;
  }
}

@media (max-width: 768px) {
  .section_main .wrap .sidebar {
    display: grid;
    grid-template-columns: 1fr 1fr;
    border: none;
    justify-self: center;
    grid-gap: 20px;
  }

  .section_main .wrap .sidebar .widget:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
    border: none;
    text-align: left;
    margin-right: 50px;
  }

  .section_main .wrap .sidebar .widget:nth-child(1) .title {
    margin-top: 0;
    margin-bottom: 5px;
  }

  .section_main .wrap .sidebar .widget:nth-child(2) {
    grid-column: 1;
    grid-row: 2;
    border: none;
    margin-top: -5px;
    margin-bottom: -100px;
    margin-left: 17px;
    text-align: left;
  }

  .section_main .wrap .sidebar .widget:nth-child(2) .title {
    margin-top: 0;
    margin-bottom: 5px;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) {
    grid-column: 2;
    grid-row: 1;
    margin-left: 25px;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) .slider {
    margin-top: 0;
    margin-bottom: 0;
  }

  .section_main .wrap .sidebar .widget:nth-child(4) {
    grid-column: 2;
    grid-row: 2;
    margin-left: 25px;
  }

  .section_main .wrap .sidebar .widget:nth-child(4) img {
    margin-top: -70px;
  }

  .section_main .wrap .sidebar .description {
    display: none;
  }

  .section_main .wrap .content .cards {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 560px) {
  .header .nav_menu_carousel {
    margin: 0;
  }

  .header .nav_menu_carousel .wrap {
    border-radius: 0;
  }

  .header #burger1 {
    display: block;
  }

  .header .nav_menu_top .collapse {
    flex-direction: column;
    align-items: start;
    display: none;
  }

  .header .nav_menu_top .collapse.show {
    display: flex;
  }

  .section_main .wrap .content .cards {
    grid-template-columns: 1fr;
  }

  .section_main .wrap .sidebar {
    display: flex;
    flex-direction: column;
  }

  .section_main .wrap .sidebar .widget:nth-child(3) {
    margin-top: 100px;
    margin-left: 10px;
    margin-bottom: 20px;
  }

  .section_main .wrap .sidebar .widget:nth-child(4) {
    margin-left: 10px;
  }

  .section_discount .wrap {
    display: flex;
    flex-direction: column;
  }

  .section_discount .wrap p {
    font-size: 13px;
  }

  .section_discount .wrap p span {
    font-size: 17px;
  }

  .section_discount .wrap .name {
    margin-bottom: 1px;
  }

  .section_discount .wrap .email {
    margin-bottom: 13px;
  }

  .section_slider .slider .item .caption {
    left: 50px;
  }

  .footer_top .items {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}